<template>
  <v-menu
    bottom
    left
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn
        class="ml-2"
        min-width="0"
        text
        v-bind="attrs"
        v-on="on"
      >
        <v-badge
          v-if="profile != 'CUSTOMER' && allPreOrders.length != 0"
          bordered
          color="red"
          overlap
        >
          <template v-slot:badge>
            <span>{{ allPreOrders.length }}</span>
          </template>
          <v-icon>mdi-list-box</v-icon>
        </v-badge>
        <v-badge
          v-else-if="profile === 'CUSTOMER' && customerPreOrders.length != 0"
          bordered
          color="red"
          overlap
        >
          <template v-slot:badge>
            <span>{{ customerPreOrders.length }}</span>
          </template>
          <v-icon>mdi-list-box</v-icon>
        </v-badge>
        <v-icon v-else>
          mdi-list-box
        </v-icon>
      </v-btn>
    </template>

    <v-list
      v-if="profile != 'CUSTOMER' && allPreOrders.length != 0"
      flat
      nav
      dense
    >
      <app-bar-item
        v-for="(n, i) in allPreOrders"
        :key="i"
        link
      >
        <v-list-item-content>
          <v-list-item-title>{{ n }}</v-list-item-title>
        </v-list-item-content>
      </app-bar-item>
    </v-list>
    <v-list
      v-else-if="profile === 'CUSTOMER' && customerPreOrders.length != 0"
      flat
      nav
      dense
    >
      <app-bar-item
        v-for="(n, i) in customerPreOrders"
        :key="i"
        link
      >
        <v-list-item-content>
          <v-list-item-title>{{ n }}</v-list-item-title>
        </v-list-item-content>
      </app-bar-item>
    </v-list>

    <v-list
      v-else
      disabled
    >
      <app-bar-item>Não há novas solicitações</app-bar-item>
    </v-list>
  </v-menu>
</template>

<script>
  import AppBarItem from '@/components/app/BarItem'

  export default {
    name: 'LicencesallPreOrders',

    components: { AppBarItem },

    data: () => ({
      allPreOrders: [],
      customerPreOrders: [],
    }),

    computed: {
      profile () {
        return window.sessionStorage.profile
      },
      razaoSocial () {
        return window.sessionStorage.razaosocial
      }
    },

    created () {
      this.fetchPreOrders()
    },

    methods: {
      fetchPreOrders () {
        return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((res) => res.json())
          .then((res) => {
            let allPreOrders = res.filter(r => r.STATUS === 'PRE-PEDIDO')
            let customerPre = res.filter(r => (r.EMPRESA === this.razaoSocial && r.STATUS === 'PRE-PEDIDO'))

            allPreOrders.forEach(solic => this.allPreOrders.push(solic.EMPRESA + ' - Nova solicitação de Licença'))
            customerPre.forEach(solic => this.customerPreOrders.push('Licença ID ' + solic.ID + ' Solicitada'))
          }
        )
      },
    },
  }
</script>
